import React, { Component, Fragment, forwardRef } from "react";
import UploadForm from "./UploadForm";
import Flow from '@flowjs/flow.js';
import {ToastContainer} from 'react-toastify';

//
class UploadContent extends Component {
  constructor(props) {
    super(props);
    this.state = {
        fileID: "",
        fileList: [],
        isSubmitted: false,
        isCompleted: true,
        flow: props.flow,
    };
    this.myRef=React.createRef()
    console.log("state : ", this.state);
    console.log("props : ", props);
  }

  componentWillUnmount() {
    this.props.setFlow(this.state.flow);
  }

  showProcess=(array)=>{
    this.setState({
      isSubmitted: true,
      fileList: array
    });
  }

  componentDidMount=()=>{
    let target='http://localhost/api/upload/flow';
    if(!this.state.flow||!this.state.flow.isUploading()){
      this.state.flow=new Flow({
          target: function(file, url){
              if(file.targetUrl==null){
                console.log("!!!!!!!!!!!!!!!!!!!!!!! target  !!!, file : ", file, " url : ", file.targetUrl);
                file.cancel();
                console.log('!');
                return "test.";
              }
              else
                console.log('success, file : ', file, ' url : ', file.targetUrl);
              return file.targetUrl;
          },

          simultaneousUploads : 1,
          withCredentials : true,
          chunkSize : 1*1024*1024
      });
    }
    else{
      this.showProcess(this.state.flow.files);
    }
    this.state.flow.assignBrowse(this.myRef.current);
    this.state.flow.assignDrop(this.myRef.current);

    if(!this.state.flow.support) console.log("flow.js  .");

    console.log("/ .", this.state.flow.isUploading());
    this.state.flow.on('fileAdded', function(file){
        let data= {
            fileSize: file.size,
            fileName: file.name,
            directory: '/'
        };
        console.log("data : ", data);
        const formData  = new FormData();
        for(const name in data) {
            console.log("name : ", name, data[name])
            formData.append(name, data[name]);
        }

        console.log('formData : ', formData);
        let errorCheck = response => {
            if(response.status==400){
                console.log("this : ", this);
                this.remove(file);
                throw Error("     .");
            }
            else if(response.status==401){
              throw Error(" .  .");
              this.props.history.push('/');
            }
            else if(response.status==403){
                throw Error('  .');
            }
            console.log("promise 1, response : ", response);
            return response;
        };
        console.log(" !");
        fetch("http://localhost/api/upload/flow", {
            method: "POST",
            credentials: 'include',
            body: formData,
        })
        .then(errorCheck)
        .then(res=>res.json())
        .then(response=>{ //   
            console.log("promise 2, response : ", response);
            //let url = response.headers.get('Location'); //docker   
            let url=response['Location']; //, build   
            console.log('url : ', url);
            file.targetUrl=url; //   .
            console.log('end!');
            return file;
        })
        .then(file=>{
          let isSetting=false;
          function check(file){
            if(file.targetUrl!=null&&file.targetUrl!=''){
              console.log('check!, isSetting', isSetting);
              isSetting=true;
              clearInterval(wait);
            }
            else{
              console.log('here, error check!!, repeat.');
            }
          }
          let wait=setInterval(function(){
            console.log('file : ', file);
            check(file);
            console.log('here, isSetting : ', isSetting);
            if(isSetting) file.resume();
            else console.log('not setting!!!!!');
          }, 200);
          
        })
        .catch(e=>this.props.notify(e));
        
    }.bind(this));

    this.state.flow.on('filesSubmitted', function(array, event){
      for(let i=0; i<array.length; i++){
        console.log('file ', i, '  !, url : ', array[i].targetUrl, this.myRef);     
        
      }
      console.log('   !  ', this.state.flow.files, ' this : ', this);
      this.showProcess(array);
      console.log(this.state.fileList);
    }.bind(this))

    this.state.flow.on('fileRetry', function(file, chunk){ // 
        console.log('!');
    });

    this.state.flow.on('fileRemoved', function(file){ //      
      console.log(' ', file, ' !');
      if(this.state.flow.files.length==0){
          console.log("all file removed!", this.myRef);
          this.setState({
              isSubmitted: false
          })
      }
    }.bind(this));

    this.state.flow.on('fileSuccess', function(file, message, chunk){ //     
        console.log(file, message, ' !');
        let string=file.name + '  !';
        if(file.name.length>30){
          string=<div>{file.name.substr(0, 30) + '...'}<br /> !</div>;
        }
        else if(file.name.length>20){
          string=<div>{file.name}<br /> !</div>;
        }
        console.log("string : ", string);
        this.props.notify(string);
    }.bind(this));

    this.state.flow.on('fileError', function(file, message){ //     
        console.log(file, message, "!");
    });
    
    this.state.flow.on('fileProgress', function(file, chunk){
      console.log(file.name, " ...", file.timeRemaining(), file.sizeUploaded())
      let array=this.state.fileList;
      for(let i=0; i<array.size; i++){
        if(file.uniqueIdentifier==array[i].uniqueIdentifier){
            array[i]=file;
            break;
        }
      }
      this.setState({
        fileList: array
      })
    }.bind(this))

    this.state.flow.on('complete', function(){
      console.log(" !, this : ", this);
    }.bind(this))
  }

  setUploadAreaEvent=()=>{
    this.state.flow.assignBrowse(this.myRef.current);
    this.state.flow.assignDrop(this.myRef.current);
  }

  stop=(file)=>{
    console.log("pause call!, file : ", file);
    file.pause();
    this.setState({
        fileList: this.state.fileList
    })
  }

  resume=(file)=>{
    console.log("resume call!, file : ", file);
    file.resume();
  }

  remove=(file)=>{
    console.log("remove call!, file : ", file, 'type : ', typeof(this.state.fileList), file.targetUrl);
    let list=this.state.fileList;
    const idx=this.state.fileList.findIndex((f)=>{return f.uniqueIdentifier==file.uniqueIdentifier})
    file.cancel();
    this.setState({
      fileList: list.slice(0, idx).concat(list.slice(idx+1, list.length))
    })

    if(file.targetUrl){
      let id=file.targetUrl.split('/').reverse()[0];
      let url='http://localhost/api/partial/' + id;
      console.log("id : ", id);

      let errorCheck=(response)=>{
        console.log("reponse : ", response);
        if(!response.ok){
          throw Error("  !");
        }
        return response;
      }
      fetch(url, {
        method: "DELETE",
        headers: {
          'Content-Type' : 'application/json',
        },
        credentials: 'include',
      })
      .then(errorCheck)
      .then(()=>{
        console.log('partial file  !', this.myRef);
        console.log('here!, ref : ', this.myRef);
        this.setUploadAreaEvent();
      })
      .catch(e=>this.props.notify(e))
    }

    else if(this.state.flow.files.length==0){
      console.log('here!, ref : ', this.myRef);
      this.setUploadAreaEvent();
    }
  }

  render() {
    console.log("upload render start, myRef : ", this.myRef);

    return (
      <Fragment>
            <ToastContainer />
            <div>
                <UploadForm
                    myRef={this.myRef} //    this.myRef.current    .
                    isSubmitted={this.state.isSubmitted}
                    fileList={this.state.fileList}
                    stop={this.stop}
                    resume={this.resume}
                    remove={this.remove}
                />
            </div>
      </Fragment>
    );
  }
}

export default UploadContent;
